body {
  margin:0;
  padding:0;
  font-size:16px;
  background: #CDCDCD;
}
.topH{
  background:rgba(47,47,47,0.98);
}
.header1{
  height: 50px;
  background: rgba(47,47,47,0.98);
}
header {
  height:50px;
  background:#333;
  background:rgba(47,47,47,0.98);
  width:600px;
  margin:0 auto;
}
.section{
  margin:0 auto;
  width:600px;
}
label{
  float:left;
  width:100px;
  line-height:50px;
  color:#DDD;
  font-size:24px;
  cursor:pointer;
  font-family: "Helvetica Neue",Helvetica,Arial,sans-serif;
}
header input{
  float:right;
  width:60%;
  height:24px;
  margin-top:12px;
  text-indent:10px;
  border-radius:5px;
  box-shadow: 0 1px 0 rgba(255,255,255,0.24), 0 1px 6px rgba(0,0,0,0.45) inset;
  border:none
}
input:focus{
  outline-width:0
}
h2{
  position:relative;
}
span{
  position:absolute;
  top:2px;
  right:5px;
  display:inline-block;
  padding:0 5px;
  height:20px;
  border-radius:20px;
  background:#E6E6FA;
  line-height:22px;
  text-align:center;
  color:#666;
  font-size:14px;
}
ol,ul{
  padding:0;
  list-style:none;
}
li input{
  position:absolute
  ;top:2px;
  left:10px;
  width:22px;
  height:22px;
  cursor:pointer;
}
p{
  margin: 0;
}
li p input{
  top:3px;
  left:40px;
  width:70%;
  height:20px;
  line-height:14px;
  text-indent:5px;
  font-size:14px;
}
li{
  height:32px;
  line-height:32px;
  background: #fff;
  position:relative;
  margin-bottom: 10px;
  padding:0 45px;
  border-radius:3px;
  border-left: 5px solid #629A9C;
  box-shadow: 0 1px 2px rgba(0,0,0,0.07);
}
ol li{
  cursor:move;
}
ul li{
  border-left: 5px solid #999;
  opacity: 0.5;
}
li a
{position:absolute;
  top:2px;right:5px;
  display:inline-block;
  width:14px;
  height:12px;
  border-radius:14px;border:6px double #FFF;
  background:#CCC;
  line-height:14px;
  text-align:center;color:#FFF;
  font-weight:bold;
  font-size:14px;
  cursor:pointer;
}
footer{
  color:#666;
  font-size:14px;
  text-align:center;
}
footer a{
  color:#666;
  text-decoration:none;
  color:#999;
}